<template>
  <div>
    <el-dialog
      title="提示"
      :visible.sync="dialogData.dialogVisible"
      append-to-body>
      <el-table :data="dialogData.dataList">
        <el-table-column property="name" label="name"></el-table-column>
        <el-table-column property="value" label="value"></el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogData.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogData.dialogVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import store from '@/store'

  export default {
    name: "myDialog",
    data() {
      return {
        dialogVisible: false
      };
    },
    computed: {
      dialogData() {
        return store.getters.dialogData
      }
    },
  }
</script>

<style scoped>

</style>



