const dialogData = {
  state: {
    dialogData: {
      dialogVisible: false
    }
  },
  mutations: {
    setDialogData: (state, dialogData) => {
      state.dialogData = dialogData
    }
  },
  actions: {
    setDialogDataActions({commit}, dialogData) {
      commit('setDialogData', dialogData)
    }
  }
}
export default dialogData



