import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [

    {
      path: '/dragAndResize',
      name: 'dragAndResize',
      component: () => import("../components/dragAndResize.vue")
    },
    {
      path: '/dragAndResizePreview',
      name: 'dragAndResizePreview',
      component: () => import("../components/dragAndResizePreview.vue")
    }

  ]
})



