<template>
  <div id="app">
    <layout></layout>
  </div>
</template>

<script>
  import layout from '@/components/layout'

  export default {
    name: 'App',
    components: {
      layout
    }
  }
</script>

<style>
  *{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



