import Vue from 'vue'
import Vuex from 'vuex'
import loader from './loader'
import getters from './getters'
Vue.use(Vuex)
const state = {
  userInfo: {phone: 111}, //用户信息
  orderList: [{orderno: '1111'}], //订单列表
  orderDetail: null, //订单产品详情
  login: false, //是否登录
}
const store = new Vuex.Store({
  state,
  modules: {
    ...loader
  },
  getters
})

export default store



