import axios from '@/request/request'

const user = {
  state: {
    token: '',
    name: '',
    saveDisabled: false,
    loading: true
  },
  mutations: {
    setToken: (state, token) => {
      state.token = token
    },
    setSaveDisabled: (state, saveDisabled) => {
      state.saveDisabled = saveDisabled
    },
    setLoading: (state, loading) =>{
      state.loading = loading
    }
  },
  actions: {
    changeToken({ commit }, token) {
      commit('setToken', '123456')
    },
    setSaveDisabledActions({ commit }, saveDisabled) {
      commit('setSaveDisabled', saveDisabled)
    },
    setLoadingActions({commit}, loading) {
      commit('setLoading', loading)
    },
    getLoginInfo(context, userInfo) {
      console.log(context)
      return new Promise((resolve, reject) => {
        axios.post('/OAuthPwd/GetPwdToken', {
          UserName: userInfo.UserName,
          Password: userInfo.Password,
          AppID: userInfo.AppID,
          AppSec: userInfo.AppSec,
          Scopes: userInfo.Scopes
        }).then(response => {
          console.log(response)
          console.log('登录信息')
          context.commit('setToken', response.data.data.access_token)
          resolve(response)
        }).catch(err => {
          console.log(err)
          console.log('错误信息')
          reject(err)
        })
      })
    }
  }
}
export default user



