const modules = {
  state: {
    modules: {}
  },
  mutations: {
    setModules: (state, modules) => {
      state.modules = modules
    }
  },
  actions: {
    setModulesActions({commit}, modules) {
      commit('setModules', modules)
    }
  }
}
export default modules



