const releaseDatas = {
  state: {
    releaseData: {},
    dragAndResizeArr: []
  },
  mutations: {
    setReleaseData: (state, releaseData) => {
      state.releaseData = releaseData
    },
    setDragAndResizeArr:(state,dragAndResizeArr)=>{
      state.dragAndResizeArr = dragAndResizeArr
    }
  },
  actions: {
    setReleaseDataActions({commit}, releaseData) {
      commit('setReleaseData', releaseData)
    },
    setDragAndResizeArrActions({commit}, dragAndResizeArr) {
      commit('setDragAndResizeArr', dragAndResizeArr)
    }
  }
}
export default releaseDatas



